@import "src/commun/styles/sass/variables";

.mainBlock {
  padding: 50px 0;
  background-color: $bgColor;

  .main {
    background-color: $bgColor;
    width: 100%;
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    justify-content: center;

    .text {
      color: $titleColor;
      font-family: $fontFamily;
      text-align: left;
      padding-left: 20px;
      border: 1px solid $primaryColor;
      width: 600px;
      background-color: $bgColor;
      overflow-wrap: break-word;

      .myName {
        font-family: $fontFamily;
        color: $primaryColor;
      }
    }

    .placePhoto {
      /*@media screen and (max-width: 779px){

      };*/
      width: 624px;
      min-height: 250px;
      background-color: $primaryColor;

      .photoProfile {
        width: 100%;
        height: 100%;
      }
    }
  }

}










